import { NgModule ,NO_ERRORS_SCHEMA} from '@angular/core';
import { CommonModule } from '@angular/common';
import { SharedModule } from '../../../shared/modules/shared.module';
import { FiscalYearRoutingModule } from './fiscal-year-routing.module';
import { FiscalYearComponent } from './fiscal-year.component';
import { FiscalYearAddComponent } from './fiscal-year-add/fiscal-year-add.component';
import { FiscalYearEditComponent } from './fiscal-year-edit/fiscal-year-edit.component';

@NgModule({
  imports: [
    CommonModule,
    SharedModule,
    FiscalYearRoutingModule
  ],
  entryComponents:[
    FiscalYearAddComponent,
    FiscalYearEditComponent
  ],
  declarations: [
    FiscalYearComponent,
    FiscalYearAddComponent,
    FiscalYearEditComponent
  ],
  schemas: [ NO_ERRORS_SCHEMA ]
})
export class FiscalYearModule { }



