import { Component, OnInit,Output,EventEmitter ,ViewChild } from '@angular/core';
import { FormGroup,FormControl,Validators,FormBuilder } from '@angular/forms';
import { FiscalYearService,ErrorHandleService } from '../../../../shared';
import { jqxValidatorComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxvalidator';
import { jqxLoaderComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxloader';
import { jqxDateTimeInputComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxdatetimeinput';
import { jqxComboBoxComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxcombobox';
import { jqxNotificationComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxnotification';

@Component({
  selector: 'app-fiscal-year-add',
  templateUrl: './fiscal-year-add.component.html',
  styleUrls: ['./fiscal-year-add.component.scss']
})
export class FiscalYearAddComponent implements OnInit {
  @ViewChild('jqxLoader') jqxLoader: jqxLoaderComponent;
  @ViewChild('myValidator') myValidator: jqxValidatorComponent;
  @ViewChild('startdate') startdate: jqxDateTimeInputComponent;    
  @ViewChild('enddate') enddate: jqxDateTimeInputComponent;    
  @ViewChild('cComboBox') cComboBox: jqxComboBoxComponent;    
  @ViewChild('winNotification') winNotification: jqxNotificationComponent;
  @Output() AddedData = new EventEmitter();
  @Output() cancelData = new EventEmitter();

  fiscalYearForm:FormGroup;
  name:string;
  start_date:string;
  end_date:string;
  current:string;
  user_id:string;
  cAdapter:any[]=[
    { value: '1', label: 'Yes' },
    { value: '0', label: 'No '}
   ];  

  constructor(
    private fys: FiscalYearService,
    private ehs: ErrorHandleService,
    private fb: FormBuilder,
  ) { 
    this.fiscalYearForm = this.fb.group({ 
      'name':[null, Validators.required],
      'start_date':[null, Validators.required],
      'end_date':[null, Validators.required],
     // 'current':[null, Validators.required],
    });
  }

  ngOnInit() {
  }

  saveBtn(post){ 
    post['current']=this.cComboBox.val();
    this.myValidator.validate(document.getElementById('fiscalYearForm'));
    if (post['current'] ) {
    this.jqxLoader.open();
   // post['user_id'] = 1;
    this.fys.store(post).subscribe(
      result => {
        let data = this.ehs.formatError(result);
        if (data['result'] == true) {
          this.AddedData.emit(data);
        } else {
          let messageDiv: any = document.getElementById('windowMessage');
          messageDiv.innerHTML = '';
          for (let i = 0; i < data['message'].length; i++) {
            let errorDiv = document.createElement('li');
            errorDiv.innerText = data['message'][i];
            messageDiv.appendChild(errorDiv);
          }
          this.winNotification.open();
        }
        this.jqxLoader.close();
      },
      error => {
        let data = this.ehs.formatError(error);
        let messageDiv: any = document.getElementById('windowMessage');
        messageDiv.innerText = data['message'];
        this.winNotification.open();
        this.jqxLoader.close();
    }
    );
  }
   }
   cancelBtn(){
    this.cancelData.emit(true);
   }
   rules =
   [
    { input: '.nameInput', message: 'Name is required!', action: 'keydown, blur', rule: 'required' },
    { input: '.start_dateInput', message: 'Start date is required!', action: 'keydown, blur', rule: (input: any, commit: any): any => {
      let data = this.startdate.val();
      let result = (data!='')?true:false;
      return result;
    } },
    { input: '.end_dateInput', message: 'End date is required!', action: 'keydown, blur', rule: (input: any, commit: any): any => {
      let data = this.enddate.val();
      let result = (data!='')?true:false;
      return result;
    } },
    { input: '.currentInput', message: 'Current is required!', action: 'keydown, blur', rule: 'required' },
       
   ];

}



